.navbar-header {
    display: flex;
    background-color: #333;
    justify-content: space-between;
    align-items: center;
    padding: 5px 5%;
  }
  
.navbar-ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}
  
.navbar-li {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: #ecf0f1;
    float: left;
}
  
.navbar-li a {
    display: block;
    color: white;
    text-align: center;
    padding: 24px 16px;
    text-decoration: none;
    border-radius: 10px;
}
  
  /* Change the link color to #111 (black) on hover */
  
.navbar-li a:hover {
    border-radius: 20px;
    -webkit-transition: 1s;
    transition: 1s;
    background-color: var(--theme-color);
}

  
.logo {
    flex-shrink: 0;
    max-height: 10vh;
}

.open-menu-btn {
    float: right;
    color: white;
    font-size: 35px; 
    display: none;
    cursor: pointer;
}

.close-btn {
    display: none;
    font-size: 50px;
    cursor: pointer;
    color: white;
}

.close-btn:hover, .open-menu-btn:hover {
    -webkit-transition: 0.2s;
    transition: 0.2s;
    color: var(--theme-color);
}

#chk {
    position: absolute;
    visibility: hidden;
}

@media screen and (max-width: 530px) {
    .close-btn {
        display: block;
        position: absolute;
        top: 40px;
        right: 40px;
    }

    .open-menu-btn{
        display: block;
    }

    .navbar-ul {
        position: fixed;
        width: 100%;
        height: 100vh;
        left: 100vh;
        right: 0;
        top: 0;
        padding: 80px;
        line-height: normal;
        -webkit-transition: 0.7s;
        transition: 0.7s;
    }
    .navbar-li {
      padding: 10px;  
      float: none;
      display: block;
    }

    #chk:checked ~ .navbar-ul {
        left: 0;
    }

    

  }

